// components/SubmissionCard.tsx
'use client'

import { useEffect, useRef, useState } from 'react'
import { useRouter } from 'next/navigation'

type Props = {
  id: string
  imageUrl?: string
  description: string
  createdAt: string
  reverse?: boolean
}

const UNDO_SECONDS = 10

export default function SubmissionCard({
  id,
  imageUrl,
  description,
  createdAt,
  reverse = false,
}: Props) {
  const router = useRouter()

  const [isEditing, setIsEditing] = useState(false)
  const [text, setText] = useState(description)
  const [saving, setSaving] = useState(false)
  const [deleting, setDeleting] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const [pendingDelete, setPendingDelete] = useState(false)
  const [countdown, setCountdown] = useState(UNDO_SECONDS)
  const timeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null)
  const intervalRef = useRef<ReturnType<typeof setInterval> | null>(null)
  const [gone, setGone] = useState(false)

  useEffect(() => {
    return () => {
      if (timeoutRef.current) clearTimeout(timeoutRef.current)
      if (intervalRef.current) clearInterval(intervalRef.current)
    }
  }, [])

  const onSave = async () => {
    setError(null)
    const trimmed = text.trim()
    if (!trimmed) { setError('Description cannot be empty.'); return }
    if (trimmed.length > 2000) { setError('Max 2000 characters.'); return }

    setSaving(true)
    try {
      const res = await fetch(`/api/submissions/${id}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ description: trimmed }),
      })
      const t = await res.text()
      if (!res.ok) {
        let msg = 'Update failed'
        try { msg = JSON.parse(t).error || msg } catch {}
        throw new Error(msg)
      }
      setIsEditing(false)
      router.refresh()
    } catch (e: any) {
      setError(e?.message || 'Update failed')
    } finally {
      setSaving(false)
    }
  }

  const finalizeHardDelete = async () => {
    try {
      const res = await fetch(`/api/submissions/${id}?hard=1`, { method: 'DELETE' })
      const t = await res.text()
      if (!res.ok) {
        let msg = 'Hard delete failed'
        try { msg = JSON.parse(t).error || msg } catch {}
        throw new Error(msg)
      }
      setGone(true)
      router.refresh()
    } catch (e: any) {
      // If hard delete fails, surface error and keep the pending state visible
      setError(e?.message || 'Hard delete failed')
      setPendingDelete(false)
      setCountdown(UNDO_SECONDS)
    } finally {
      if (timeoutRef.current) clearTimeout(timeoutRef.current)
      if (intervalRef.current) clearInterval(intervalRef.current)
      timeoutRef.current = null
      intervalRef.current = null
    }
  }

  const onDelete = async () => {
    if (pendingDelete) return
    const ok = confirm('Delete this submission? You’ll have 10 seconds to undo.')
    if (!ok) return

    setError(null)
    setDeleting(true)
    try {
      // Soft delete first
      const res = await fetch(`/api/submissions/${id}`, { method: 'DELETE' })
      const t = await res.text()
      if (!res.ok) {
        let msg = 'Delete failed'
        try { msg = JSON.parse(t).error || msg } catch {}
        throw new Error(msg)
      }

      setPendingDelete(true)
      setCountdown(UNDO_SECONDS)
      // Start countdown + hard delete timer
      intervalRef.current = setInterval(() => {
        setCountdown((c) => (c > 0 ? c - 1 : 0))
      }, 1000)
      timeoutRef.current = setTimeout(finalizeHardDelete, UNDO_SECONDS * 1000)
    } catch (e: any) {
      setError(e?.message || 'Delete failed')
    } finally {
      setDeleting(false)
    }
  }

  const onUndo = async () => {
    if (!pendingDelete) return
    setError(null)
    try {
      const res = await fetch(`/api/submissions/${id}/undo`, { method: 'POST' })
      const t = await res.text()
      if (!res.ok) {
        let msg = 'Undo failed'
        try { msg = JSON.parse(t).error || msg } catch {}
        throw new Error(msg)
      }
      // Cancel timers and restore
      if (timeoutRef.current) clearTimeout(timeoutRef.current)
      if (intervalRef.current) clearInterval(intervalRef.current)
      timeoutRef.current = null
      intervalRef.current = null
      setPendingDelete(false)
      setCountdown(UNDO_SECONDS)
      setError(null)
    } catch (e: any) {
      setError(e?.message || 'Undo failed')
    }
  }

  if (gone) return null

  const imageColOrder = reverse ? 'md:order-2' : 'md:order-1'
  const textColOrder  = reverse ? 'md:order-1' : 'md:order-2'

  return (
    <article className={`border rounded-xl p-4 grid gap-4 md:grid-cols-[260px_1fr] items-start print:border-0 print:p-0 print:shadow-none print:break-inside-avoid ${pendingDelete ? 'opacity-60' : ''}`}>
      {/* Image */}
      <div className={imageColOrder}>
        {imageUrl ? (
          <img
            src={imageUrl}
            alt="submission"
            className="w-full h-auto rounded-lg object-cover max-h-[420px]"
          />
        ) : (
          <div className="w-full aspect-video rounded-lg border grid place-items-center text-sm text-gray-500">
            No image
          </div>
        )}
      </div>

      {/* Text / Meta / Actions */}
      <div className={`grid gap-3 ${textColOrder}`}>
        {/* Normal / editing UI (hidden on print) */}
        {isEditing ? (
          <>
            <div className="grid gap-2 print:hidden">
              <textarea
                className="border rounded-xl p-3 min-h-28"
                value={text}
                onChange={(e) => setText(e.target.value)}
                maxLength={2000}
                disabled={pendingDelete}
              />
              <div className="text-sm opacity-70">{text.length}/2000</div>
              <div className="flex gap-2">
                <button
                  onClick={onSave}
                  disabled={saving || pendingDelete}
                  className="border rounded-xl px-3 py-1"
                >
                  {saving ? 'Saving…' : 'Save'}
                </button>
                <button
                  onClick={() => { setIsEditing(false); setText(description) }}
                  disabled={saving || pendingDelete}
                  className="border rounded-xl px-3 py-1"
                >
                  Cancel
                </button>
              </div>
            </div>
            <p className="whitespace-pre-wrap hidden print:block">{description}</p>
          </>
        ) : (
          <p className="whitespace-pre-wrap">{description}</p>
        )}

        <time className="text-sm opacity-70">
          {new Date(createdAt).toLocaleString()}
        </time>

        {error ? <p className="text-red-600 text-sm print:hidden">{error}</p> : null}

        {/* Delete/Edit (hidden on print) */}
        <div className="flex gap-2 print:hidden">
          <button
            onClick={() => setIsEditing(true)}
            disabled={deleting || saving || pendingDelete}
            className="border rounded-xl px-3 py-1"
          >
            Edit
          </button>
          <button
            onClick={onDelete}
            disabled={deleting || saving || pendingDelete}
            className="border rounded-xl px-3 py-1"
          >
            {deleting ? 'Deleting…' : 'Delete'}
          </button>
        </div>

        {/* Undo bar (screen only) */}
        {pendingDelete && (
          <div className="flex items-center gap-3 bg-amber-50 border border-amber-200 rounded-xl p-3 print:hidden">
            <span>Deleted.</span>
            <button
              onClick={onUndo}
              className="border rounded-xl px-3 py-1"
            >
              Undo ({countdown})
            </button>
          </div>
        )}
      </div>
    </article>
  )
}
